<template>
  <div class="todo-list">
    <Todo
      class="todo-item"
      v-for="todo in value" :key="todo.id"
      :value="todo"
      @input="updateTodo"
    />
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
import { ACTIONS } from "../store/modules/todo-store-types"

import Todo from "./Todo";

const { mapActions } = createNamespacedHelpers('todo');

export default {
  name: "TodoList",

  components: {
    Todo
  },

  props: {
    value: Array
  },

  methods: {
    ...mapActions({
      updateTodo: ACTIONS.UPDATE_TODO
    })
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/styles/colors.scss";

.todo-list {
  margin-top: 2rem;
  width: 100%;
  background-color: $base-color-smoke;
  padding: 0.75rem;
}

.todo-item {
  margin-top: 0.25rem;
}
</style>
