import Vue from "vue";
import { MUTATIONS, ACTIONS } from "./todo-store-types"
import * as apiTodo from "../../service/api/todos"

export default {
  namespaced: true,

  state: {
    todoList: [],
  },

  getters: {
    todoListCompleted: state => {
      return state.todoList.filter(todo => todo.done).sort((a, b) => a.id - b.id)
    },

    todoListNotCompleted: state => {
      return state.todoList.filter(todo => !todo.done).sort((a, b) => a.id - b.id)
    },
  },

  mutations: {
    [MUTATIONS.ADD_TODO] (state, todo) {
      state.todoList.push(todo)
    },

    [MUTATIONS.SET_TODO_LIST] (state, todoList) {
      Vue.set(state, 'todoList', todoList)
    }
  },

  actions: {
    [ACTIONS.FETCH_TODOS]: async (context) => {
      const response = await apiTodo.getAllTodos();
      context.commit(MUTATIONS.SET_TODO_LIST, response.data.todos);
    },

    [ACTIONS.CREATE_TODO]: async (context, newTodo) => {
      const response = await apiTodo.createTodo(newTodo.description);
      context.commit(MUTATIONS.ADD_TODO, response.data)
    },

    [ACTIONS.UPDATE_TODO]: async (context, todoToUpdate) => {

      if(todoToUpdate.description === null) {
        await apiTodo.deleteTodo(todoToUpdate.id);
      } else {
        await apiTodo.updateTodo(todoToUpdate);
      }

      await context.dispatch(ACTIONS.FETCH_TODOS);
    }
  },

  modules: {},
}
