<template>
  <div class="todo-app">

  <!-- TodoFactory √© um componente capaz de gerar Todo's -->
    <TodoFactory @new-todo="createTodo" />

    <div v-show="todoListApp.length === 0">Voc√™ n√£o tem nenhum TODO ü§î</div>
    <div v-show="todoListApp.length !== 0">TODO's ‚ñ∂Ô∏è</div>
    <!-- TodoList √© um componente capaz de gerenciar um array de Todo's -->
    <TodoList :value="todoList" />

    <div v-show="todoDoneList.length === 0">Voc√™ n√£o tem nenhum TODO conclu√≠do ‚ÑπÔ∏è</div>
    <div v-show="todoDoneList.length !== 0 && todoDoneList.length !== todoListApp.length">
      TODO's conclu√≠dos ‚úÖ
    </div>
    <div v-show="todoDoneList.length === todoListApp.length && todoListApp.length !== 0">
      Parab√©ns! Voc√™ concluiu todos os TODO's! ü•≥
    </div>
    <!-- TodoList √© um componente capaz de gerenciar um array de Todo's -->
    <TodoList :value="todoDoneList" />
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
import { ACTIONS } from "../store/modules/todo-store-types"

import TodoFactory from "./TodoFactory"
import TodoList from "./TodoList"

const { mapState, mapGetters, mapActions } = createNamespacedHelpers('todo');

export default {
  name: "TodoApp",

  components: {
    TodoFactory,
    TodoList
  },

  computed: {
    ...mapState({
      todoListApp: 'todoList',
    }),
    ...mapGetters({
      todoList: 'todoListNotCompleted',
      todoDoneList: 'todoListCompleted',
    }),
  },

  mounted: async function () {
    await this.fetchTodos();
  },

  methods: {
    ...mapActions({
      createTodo: ACTIONS.CREATE_TODO,
      fetchTodos: ACTIONS.FETCH_TODOS
    })
  }
}
</script>

