<template>
  <div class="new-todo-container">
    <input
      type="text"
      class="n-input-text"
      placeholder="Ex: Comprar um leite..."
      v-model.trim="todoDescription"
      @keyup.enter="emitNewTodo"
    />
    <button class="n-icon" @click="emitNewTodo" >
      <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="plus" class="svg-inline--fa fa-plus fa-w-14" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512">
        <path fill="currentColor" d="M416 208H272V64c0-17.67-14.33-32-32-32h-32c-17.67 0-32 14.33-32 32v144H32c-17.67 0-32 14.33-32 32v32c0 17.67 14.33 32 32 32h144v144c0 17.67 14.33 32 32 32h32c17.67 0 32-14.33 32-32V304h144c17.67 0 32-14.33 32-32v-32c0-17.67-14.33-32-32-32z">
        </path>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  name: "TodoFactory",

  data: () => ({
    todoDescription: ""
  }),

  methods: {
    emitNewTodo() {
      if (this.todoDescription === "") {
        return;
      }

      // Somente emit eventos para Todo's n√£o nulos
      this.$emit('new-todo', { description: this.todoDescription });
      this.todoDescription = "";
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/styles/icon.scss";

.new-todo-container {
  width: 100%;
  height: 2.25rem;
  margin-top: 1.5rem;
  display: flex;

  input {
    flex: 1 1 0%;
    margin-right: 0.5rem;
  }

  button {
    width: min-content;
  }
}

</style>
