<template>
  <div class="todo">
    <label class="n-container-checkbox">
      <input
        type="checkbox"
        :checked="value.done"
        @change="doneTodo"
      />
      <div class="n-checkmark"></div>
    </label>
    <input
      type="text"
      class="n-input-text"
      ref="inputDescription"
      :disabled="!editingTodo"
      :value="value.description"
      @blur="endEditDescription"
      @keyup.enter="endEditDescription"
    />
    <button class="n-icon" @click="startEditDescription">
      <svg aria-hidden="true" focusable="false" data-prefix="far" data-icon="edit" class="svg-inline--fa fa-edit fa-w-18" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
        <path fill="currentColor" d="M402.3 344.9l32-32c5-5 13.7-1.5 13.7 5.7V464c0 26.5-21.5 48-48 48H48c-26.5 0-48-21.5-48-48V112c0-26.5 21.5-48 48-48h273.5c7.1 0 10.7 8.6 5.7 13.7l-32 32c-1.5 1.5-3.5 2.3-5.7 2.3H48v352h352V350.5c0-2.1.8-4.1 2.3-5.6zm156.6-201.8L296.3 405.7l-90.4 10c-26.2 2.9-48.5-19.2-45.6-45.6l10-90.4L432.9 17.1c22.9-22.9 59.9-22.9 82.7 0l43.2 43.2c22.9 22.9 22.9 60 .1 82.8zM460.1 174L402 115.9 216.2 301.8l-7.3 65.3 65.3-7.3L460.1 174zm64.8-79.7l-43.2-43.2c-4.1-4.1-10.8-4.1-14.8 0L436 82l58.1 58.1 30.9-30.9c4-4.2 4-10.8-.1-14.9z">
        </path>
      </svg>
    </button>
    <button class="n-icon" @click="deleteThisTodo">
      <svg aria-hidden="true" focusable="false" data-prefix="far" data-icon="trash-alt" class="svg-inline--fa fa-trash-alt fa-w-14" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512">
        <path fill="currentColor" d="M268 416h24a12 12 0 0 0 12-12V188a12 12 0 0 0-12-12h-24a12 12 0 0 0-12 12v216a12 12 0 0 0 12 12zM432 80h-82.41l-34-56.7A48 48 0 0 0 274.41 0H173.59a48 48 0 0 0-41.16 23.3L98.41 80H16A16 16 0 0 0 0 96v16a16 16 0 0 0 16 16h16v336a48 48 0 0 0 48 48h288a48 48 0 0 0 48-48V128h16a16 16 0 0 0 16-16V96a16 16 0 0 0-16-16zM171.84 50.91A6 6 0 0 1 177 48h94a6 6 0 0 1 5.15 2.91L293.61 80H154.39zM368 464H80V128h288zm-212-48h24a12 12 0 0 0 12-12V188a12 12 0 0 0-12-12h-24a12 12 0 0 0-12 12v216a12 12 0 0 0 12 12z">
        </path>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  name: "Todo",

  props: {
    value: Object
  },

  data: () => ({
    editingTodo: false
  }),

  methods: {
    deleteThisTodo() {
      const newValue = {
        ...this.value,
        description: null
      }
      this.emitUpdate(newValue)
    },
    startEditDescription() {
      this.editingTodo = true;
      // Espera um pouco antes de dar o foco no elemento
      setTimeout(() => this.$refs.inputDescription.focus(), 50);
    },
    endEditDescription(event) {
      this.editingTodo = false;
      const { value } = event.target;
      const newValue = {
        ...this.value,
        description: value === "" ? null : value
      }
      this.emitUpdate(newValue)
    },
    emitUpdate(newValue) {
      this.$emit('input', newValue)
    },
    doneTodo(event) {
      const { checked } = event.target;
      const newValue = {
        ...this.value,
        done: checked
      }
      this.emitUpdate(newValue)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/styles/icon.scss";

.todo {
  display: flex;
  height: 2.25rem;

  input[type=text] {
    flex: 1 1 0%;
    margin: 0 0.5rem;
  }

  button {
    width: min-content;

    &:last-child {
      margin-left: 0.5rem;
    }
  }

}

</style>