import httpClient from './httpClient';

const ENDPOINT = '/todo';

const getAllTodos = () => httpClient.get(ENDPOINT);
const getTodo = (todoId) => httpClient.get(ENDPOINT, { todoId });
const createTodo = (description) => httpClient.post(ENDPOINT, { description });
const updateTodo = (todo) => httpClient.put(`${ENDPOINT}/${todo.id}`, todo);
const deleteTodo = (todoId) => httpClient.delete(`${ENDPOINT}/${todoId}`);

export {
  getAllTodos,
  getTodo,
  createTodo,
  updateTodo,
  deleteTodo
}
